import { motion } from "framer-motion";

const sVariant = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: {
      duration: 0.5,
    },
  },
};
//js fills
const JsVariant = {
  hidden: {
    pathLength: 0,
    fill: "rgba(0,0,0,0)",
  },
  visible: {
    pathLength: 1,
    fill: "rgba(0,0,0,1)",
    transition: {
      fill: {
        duration: 0.5,
        delay: 1,
      },
      duration: 2,
      ease: "easeInOut",
    },
  },
};

//react

